@import url('https://fonts.googleapis.com/css2?family=DM+Sans&display=swap');

body {
  font-family: 'DM Sans', sans-serif;
  font-stretch: expanded;
  color: #666666;
}

.container {
  width: 100%;
  padding: 20px 0;
  max-width: 1000px;
  margin-left: auto;
  margin-right: auto;
}

.app-wrapper {
  margin-left: auto;
  margin-right: auto;
  max-width: 900px;
}

.autosave {
  display: inline-block;
  font-family: 'DM Sans', sans-serif;
  font-weight: 100;
  font-size: 20px;
  border: 1px solid #42bfc7;
  border-radius: 0.3em;
  padding: 45px 25px 40px 25px;
  margin-bottom: 40px;
}

.header h4 {
  color: #1d9988;
}

#autosave-heading {
  display: inline;
  font-size: 18px;
  color: #436cf6;
  margin-left: 10%;
}

.subheader {
  display: flex;
  justify-content: space-between;
  padding-left: 10px;
  padding-right: 40px;
  background-color: #efefef;
  color: #0047f5;
  font-size: 30px;
  border: 1px solid #e1e1e1;
  box-shadow: 1px 1px 1px white inset;
}

#ellipsis {
  margin: auto 0;
  font-size: 45px;
  text-decoration: none;
}

a:visited {
  color: #0047f5;
}

.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 22px;
  margin-left: 60%;
}

@media (max-width: 891px) {
  .switch {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 22px;
    margin-left: 50%}
}

@media (max-width: 692px) {
  .switch {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 22px;
    margin-left: 40%}
}
@media (max-width: 573px) {
  .switch {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 22px;
    margin-left: 30%}
}
@media (max-width: 375px) {
  .switch {
    position: relative;
    display: inline-block;
    width: 40px;
    height: 22px;
    margin-left: 25%}
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #c3c3c3;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 1px;
  bottom: 1px;
  background-color: white;
  transition: 0.4s;
}

.slider:after {
  content: "";
}

input:checked + .slider {
  background-color: #0146f5;
}

input:checked + .slider::before {
  transform: translateX(18px);
}

.slider.rounded {
  border-radius: 34px;
}

.slider.rounded:before {
  border-radius: 50%;
}

.result {
  font-size: 20px;
  right: 50px;
  margin-left: auto;
  margin-right: 10px;
  color: #555555;
}

.result-second {
  position: absolute;
  font-size: 20px;
  color: #333333;
}

.summary {
  display: flex;
  justify-content: space-between;
  padding-left: 10px;
  padding-right: 40px;
  letter-spacing: 1px;
  margin: auto;
  background-color: #0146f5;
  color: white;
  font-size: 22px;
  border: 1px solid grey;
}

.summary-result {
  margin: auto 0;
}

.accordion-section {
  display: flex;
  flex-direction: column;
}

.accordion {
  background-color: #efefef;
  cursor: pointer;
  display: flex;
  align-items: center;
  border: 1px solid #e1e1e1;
  box-shadow: 1px 1px 1px white inset;
  outline: none;
  transition: background-color 0.6s ease;
}

.accordion-title {
  font-family: 'DM Sans', sans-serif;
  font-size: 20px;
  margin-left: 10px;
  letter-spacing: 0.05em;
  color: #555555;
}

.accordion-icon {
  transition: transform 0s ease;
  margin-left: 8px;
}

.rotate {
  transform: rotate(180deg);
}

.accordion-content {
  background-color: white;
  overflow: hidden;
  transition: max-height 0.6s ease;
  max-height: 0px;
}

.accordion-container {
}

